import React, { Componen, Component } from "react";
import {
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button,
  IconButton,
  CardMenu,
} from "react-mdl";
import { Link } from "react-router-dom";

class Dashboard extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={1} align="middle">
          </Cell>
          <Cell col={10} align="middle">
            <h1>Streaming Twitter</h1>
            <Card shadow={0} style={{ width: "auto", margin: "auto" }}>
              <CardTitle
                style={{
                  color: "#fff",
                  height: "176px",
                  background:
                    "url(https://cdn.pixabay.com/photo/2018/04/14/18/06/twitter-3319619_1280.jpg) center / cover",
                }}
              ></CardTitle>
              <CardText>
                Este dashboard muestra un proyecto de straming de datos de
                twitter, ejecutado con Python, MongoDB y PowerBI.
              </CardText>
              <CardActions border>
                <a
                  href="https://ggalvezb.github.io/streaming_twitter/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <Button colored>VER PAGINA</Button>
                </a>
              </CardActions>
              <CardMenu style={{ color: "#fff" }}></CardMenu>
            </Card>
          </Cell>
         </Grid>

         <Grid>
         <Cell col={1} align="middle">
          </Cell> 
          <Cell col={10} align="middle">
            <h1>Reporte Nacional de Campamentos</h1>
            <Card shadow={0} style={{ width: "auto", margin: "auto" }}>
              <CardTitle
                style={{
                  color: "#fff",
                  height: "176px",
                  background:
                    "url(https://www.minvu.cl/wp-content/uploads/2019/08/mapachile-campamentos.svg) center / cover",
                }}
              ></CardTitle>
              <CardText>
                Este dashboard muestra los resultados del catastro nacional de campamentos 2019 realizado por el MINVU
              </CardText>
              <CardActions border>
                <a
                  href="https://ggalvezb.github.io/Dashboard_campamentos/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <Button colored>VER PAGINA</Button>
                </a>
              </CardActions>
              <CardMenu style={{ color: "#fff" }}></CardMenu>
            </Card>
          </Cell>
        </Grid>

        <Grid>
         <Cell col={1} align="middle">
          </Cell> 
          <Cell col={10} align="middle">
            <h1>Reporte de Acceso a servicios en Proyectos Habitacionales Sociales
            </h1>
            <Card shadow={0} style={{ width: "auto", margin: "auto" }}>
              <CardTitle
                style={{
                  color: "#fff",
                  height: "176px",
                  background:
                    "url(https://cdn.pixabay.com/photo/2015/09/05/19/43/apartments-924786_1280.jpg) center / cover",
                }}
              ></CardTitle>
              <CardText>
                Este dashboard muestra la conectividad de los diferentes proyectos habitacionales sociales que hay en Chile,
                calculando métricas de distancia a Bomberos, Escuelas, Carabineros, etc.
              </CardText>
              <CardActions border>
                <a
                  href="https://ggalvezb.github.io/reporte_proyectos_sociales/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <Button colored>VER PAGINA</Button>
                </a>
              </CardActions>
              <CardMenu style={{ color: "#fff" }}></CardMenu>
            </Card>
          </Cell>
        </Grid>


      </div>
    );
  }
}
export default Dashboard;
